<template>
  <div class="orderList">
    <div class="navList">
      <van-tabs v-model="active" :line-width="20" :line-height="3" :swipe-threshold="6" :title-active-color="'#ef5852'" @click="onClick">
        <van-tab v-for="(item,index) in orderClassifyList" :key="index" :title="item.label"></van-tab>
      </van-tabs>
      <div class="mid">
        <div class="list mescroll" id="list">
          <div class="list-data" id="listData">
            <div class="item" v-for="(item,index) in list" :key="index">
              <div class="item-top flex" @click="toDetail(item.id)">
                <div class="No flex-2">:{{item.orderno}}</div>
                <div class="status" :style="item.orderStatus == 1 || item.orderStatus == 2?'color:#eb5252':item.orderStatus == 3?'color:#333':'color:#999'">{{$utils.getText(item.orderStatus)}}</div>
              </div>
              <div>
                <div class="item-goods flex" v-for="(item1,index1) in item.scEcomOrdersDetailList" :key="index1" @click="toDetail(item.id)">
                  <div class="goods-pic">
                    <img :src="item1.scEcomGoods?$utils.dealImg(item1.scEcomGoods.gimg):''" />
                  </div>
                  <div class="flex-2">
                    <p class="title">{{item1.scEcomGoods?item1.scEcomGoods.gname:''}}</p>
                    <div class="price">
                      
                      <span class="money">{{item1.scEcomGoods?item1.scEcomGoods.goutPrice.toFixed(2):''}}</span>
                      <span class="num">{{item1.gorderCount}}</span>
                    </div>
                  </div>
                </div>
              </div>
              <div class="orderInfo" @click="toDetail(item.id)">
                <div class="price">{{item.shopCount}} {{item.oorderMoneyShifu.toFixed(2)}}</div>
              </div>
              <div class="item-bottom">
                <div class="btn btnPay" v-if="item.orderStatus == 2" @click="toDetail(item.id)"></div>
                <div class="btn" v-else @click="toDetail(item.id)"></div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { Tab, Tabs, Dialog } from "vant";
import { getOrderList2 } from "@/api";
import MeScroll from "mescroll.js";
export default {
  components: {
    [Tab.name]: Tab,
    [Tabs.name]: Tabs
  },
  data() {
    return {
      active: 0,
      activeValue: "",
      orderClassifyList: [
        { label: "", value: "" },
        { label: "", value: "1" },
        { label: "", value: "2" },
        { label: "", value: "3" },
        { label: "", value: "5" }
      ],
      mescroll: null,
      list: []
    };
  },
  methods: {
    onClick(name, title) {
      this.activeValue = this.orderClassifyList[name].value;
      this.mescroll.scrollTo(0, 0);
      this.mescroll.resetUpScroll();
    },
    toDetail(id) {
      this.$router.push({
        name: "merchantsOrderDetail",
        query: { id: id }
      });
    },

    loadData(pageNum, pageSize, successCallback, errorCallback) {
      let $this = this;
      let dataObj = {
        pageNum: pageNum,
        pageSize: pageSize,
        orderStatus: this.activeValue
      };
      getOrderList2(dataObj)
        .then(res => {
          if (res.code == 0) {
            successCallback && successCallback(res.data);
          } else {
            $this.$toast.fail(res.errorMsg);
          }
        })
        .catch(res => {
          errorCallback && errorCallback();
          console.log(res);
        });
    },
    startMescroll() {
      this.mescroll = new MeScroll("list", {
        //vuemountedmescroll,id
        up: {
          callback: this.upCallback, //
          //,
          isBounce: false, //ios,(,): http://www.mescroll.com/qa.html#q10
          page: { size: 10 }, //8,10
          empty: {
            //
            warpId: "listData",
            icon: require("@/assets/img/nodata.png"),
            tip: ""
            //						  	btntext : " >" ,
            //						  	btnClick : function() {
            //						  		alert("");
            //						  	}
          },
          noMoreSize: 10,
          scrollbar: { use: false },
          htmlNodata: '<p class="upwarp-nodata">--  --</p>',
          lazyLoad: {
            use: true // ,false
          }
        }
      });
    },
    // page = {num:1, size:10}; num: ,1; size:,10
    upCallback: function(page) {
      //
      let $this = this;
      $this.loadData(
        page.num,
        page.size,
        function(curPageData) {
          //curPageData=[]; //,empty

          // (clearIdclearEmptyId)
          console.log(curPageData);
          if (page.num == 1) $this.list = [];
          if (curPageData.list) {
            curPageData.list.map(item => {
              $this.list.push(item);
            });
          }
          //,;
          //mescroll,,;,;
          //   console.log("page.num="+page.num+", page.size="+page.size+", curPageData.length="+curPageData.rows.length+", $this.goodsList.length==" + $this.pdlist.length);
          console.log(page);

          //():  totalSize
          $this.mescroll.endBySize(
            curPageData.list ? curPageData.list.length : 0,
            curPageData.count
          ); //(, )
        },
        function() {
          //,;
          $this.mescroll.endErr();
        }
      );
    }
  },
  mounted() {
    document.title = "";
    this.startMescroll();
  }
};
</script>
<style lang="scss" scoped>
.orderList {
  .navList {
    margin-bottom: 10px;
  }
  .mid {
    height: calc(100vh - 44px);
  }
  .list {
    border-top: 1pt solid #efefef;
    .item {
      padding: 15px 10px;
      border-bottom: 1pt solid #efefef;
      background: #fff;
      .flex {
        display: flex;
        .flex-1 {
          flex: 1;
        }
        .flex-2 {
          flex: 2;
          position: relative;
        }
      }
      .item-top {
        font-size: 15px;
        line-height: 20px;
        .No {
          color: #333;
        }
      }
      .item-goods {
        margin: 15px 0;
        .goods-pic {
          width: 100px;
          height: 100px;
          margin-right: 10px;
          position: relative;
          img {
            width: 100%;
            height: 100%;
          }
        }
        .title {
          font-size: 16px;
          line-height: 25px;
          max-height: 50px;
          text-overflow: -o-ellipsis-lastline;
          overflow: hidden;
          text-overflow: ellipsis;
          display: -webkit-box;
          -webkit-line-clamp: 2;
          line-clamp: 2;
          -webkit-box-orient: vertical;
        }
        .parameter {
          color: #999;
          font-size: 15px;
          line-height: 20px;
          margin: 5px 0 10px;
        }
        .price {
          font-size: 13px;
          font-weight: 600;
          color: #333;
          flex: 2;
          position: absolute;
          width: 100%;
          bottom: 10px;
          .money {
            font-size: 16px;
          }
          .num {
            color: #999;
            margin-left: 10px;
          }
        }
      }
      .orderInfo {
        font-size: 15px;
        .price {
          font-size: 14px;
          text-align: right;
          color: #666;
        }
      }
      .item-bottom {
        text-align: center;
        overflow: hidden;
        padding: 15px 0;
        .btn {
          width: 80px;
          color: #666;
          border: 1pt solid #e5e5e5;
          float: right;
          height: 30px;
          line-height: 30px;
          border-radius: 5px;
          font-size: 13px;
          margin-left: 5px;
        }
        .btnPay {
          color: #fff;
          background: $base-color;
          border: 1pt solid $base-color;
        }
      }
    }
  }
}
</style>
